/*------------------------------------------------------------
	Footer
------------------------------------------------------------*/
.c-footer {
  background: #000936;
  position: relative;
  z-index: 1;
  padding: 100px 0 0;
  margin-top: 12.5vw;
  &::before {
    content: "";
    width: 100%;
    height: 7.292vw;
    background: url(/assets/images/bg_wave_top.svg) no-repeat center bottom;
    background-size: 100%;
    position: absolute;
    top: -7.292vw;
    left: 0;
    z-index: 0;
  }
  &::after {
    content: "";
    width: 100%;
    height: 100%;
    background: #000936;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
  }
  .inner {
    position: relative;
    z-index: 2;
  }
  .footer {
    &__cont {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -22px -40px;
    }
    &__block {
      width: calc((100% - 132px) / 3);
      color: #fff;
      margin: 0 22px 40px;
    }
    &__frame {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      .f_ttl {
        border-bottom: 1px solid #fff;
        color: #f5ecb5;
        font-size: 20px;
        font-weight: 700;
        padding-bottom: 15px;
        margin-bottom: 20px;
      }
      .f_txt01 {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 10px;
      }
      .f_list {
        &:not(:last-child) {
          margin-bottom: 15px;
        }
        li {
          position: relative;
          display: flex;
          margin-bottom: 5px;
          &::before {
            content: "";
            width: 6px;
            height: 10px;
            display: block;
            background: url(/assets/images/icon03.png) no-repeat center;
            background-size: contain;
            position: relative;
            top: 8px;
            margin-right: 10px;
          }
        }
      }
      .list-social {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px -30px;
        > li {
          width: 30px;
          margin: 0 15px 30px;
        }
      }
      .f_txt02 {
        display: flex;
        font-size: 18px;
        font-weight: 700;
        position: relative;
        padding-left: 25px;
        margin-bottom: 10px;
        > a {
          color: #fff;
          font-weight: 400;
          display: inline-block;
          position: relative;
          margin-left: 5px;
          &:not(:last-child) {
            &::after {
              content: "-";
              display: inline-block;
              font-weight: 600;
              margin-left: 5px;
            }
          }
        }
        &::before {
          content: "";
          position: absolute;
          top: 5px;
          left: 0;
        }
        &.icon01::before {
          width: 12px;
          height: 17px;
          background: url(/assets/images/f_ico01.png) no-repeat center;
          background-size: contain;
        }
        &.icon02::before {
          width: 14px;
          height: 14px;
          background: url(/assets/images/f_ico02.png) no-repeat center;
          background-size: contain;
        }
        &.phone {
          display: flex;
          flex-wrap: wrap;
          a {
            pointer-events: none;
          }

          &::before {
            width: 15px;
            height: 15px;
            background: url(/assets/images/f_ico03.png) no-repeat center;
            background-size: contain;
          }
        }
        &.mail {
          display: flex;
          flex-wrap: wrap;
          &::before {
            width: 16px;
            height: 11px;
            background: url(/assets/images/email_icon02.png) no-repeat center;
            background-size: contain;
            top: 10px;
          }
        }
      }
    }
    &__contact {
      border-top: 1px solid #fff;
      padding: 20px 0 0;
      p {
        font-weight: 700;
        &:not(:last-child) {
          margin-bottom: 15px;
        }
      }
      .register {
        display: flex;
        margin-bottom: 15px;
        .btn_submit {
          width: 60px;
          height: 50px;
          border-radius: 0px 5px 5px 0px;
          flex-shrink: 0;
        }
        .cus_input {
          width: 100%;
          input {
            width: 100%;
            height: 50px;
            padding: 10px;
            font-weight: 400;
            box-sizing: border-box;
            border: none;
            border-radius: 5px 0 0 5px;
            &::placeholder {
              color: #7c7c7c;
              font-style: italic;
            }
          }
        }
      }
    }
  }
  address {
    background: #0779f1;
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    font-style: normal;
    text-align: center;
    position: relative;
    z-index: 9;
    padding: 22px 3vw;
    margin-top: 100px;
  }
  @include max(1024px) {
    .footer__cont {
      margin: 0 -10px -30px;
    }
    .footer__block {
      width: calc((100% - 60px) / 3);
      margin: 0 10px 20px;
    }
    .footer__frame .f_ttl {
      font-size: 18px;
    }
    .footer__frame .f_txt01 {
      font-size: 15px;
    }
    .footer__frame .list-social {
      margin: 0 -8px -16px;
    }
    .footer__frame .list-social > li {
      margin: 0 8px 16px;
    }
    .footer__frame .f_txt02.phone a i {
      pointer-events: inherit;
    }
    address {
      font-size: 15px;
      padding: 15px 3vw;
      margin-top: 7vw;
    }
  }
  @include sp {
    margin-top: 125px;
    .footer__cont {
      max-width: 500px;
      flex-direction: column;
      align-items: center;
      margin: 0 auto;
    }
    .footer__block {
      width: 100%;
      margin: 0 0 30px;
      &:last-child {
        margin: 0;
      }
    }
  }
}
